body {
  margin: 0;
  padding: 0;
  color: #ececec;
  min-width: 320px;
  background-color: #0b091c;
  background-image: url("https://i.pinimg.com/736x/4c/01/ce/4c01cec637ff4883f80c9dad12af7376.jpg");
  background-repeat: no-repeat;
  background-size: 100%; }

html {
  box-sizing: border-box;
  overflow-y: scroll;
  /* margin-left: calc(100vw - 100%);
  margin-right: 0; */ }

*,
*::before,
*::after {
  box-sizing: inherit; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

p {
  margin: 0;
  padding: 0; }

ul {
  padding: 0;
  margin: 0; }

li {
  list-style: none; }

.container {
  max-width: 480px;
  margin: 0 auto; }

.header {
  width: 100%;
  height: 70px;
  background-color: #09345a; }

.main {
  padding: 70px 0; }

.choise__link {
  display: block;
  margin-bottom: 50px; }
  .choise__link img {
    display: block;
    max-width: 100%;
    margin: 0 auto; }

.survivor__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  .survivor__list li {
    max-width: 100px;
    height: 150px;
    margin: 0 auto; }
    .survivor__list li img {
      max-width: 100%; }
    .survivor__list li p {
      text-align: center; }
